<script>


import VueSlickCarousel from "vue-slick-carousel";
import axios from "axios";
import "vue-slick-carousel/dist/vue-slick-carousel-theme.css";
import '../assets/css/main.css';
import $ from "jquery";


export default {
  
   name: "HomeView",
components: { VueSlickCarousel },

     data() {
      return {
          instafeed: null,
        value: '',
        
      }
    }, 
   methods: {
 getInsta() {
   axios.get(`https://graph.facebook.com/v5.0/1750464891898763/mediafields=media_url%2Ctimestamp%2Cthumbnail_url%2Ccaption&access_token ${process.env.VUE_APP_FB_ACCESS}`)
  .then((response) => {
   this.instafeed = response.data.data;
  });
 },
},
created() {
  this.getInsta();
 },

    mounted() {
       function bootnavbar(options) {
  const defaultOption = {
    selector: "main_navbar",
    animation: true,
    animateIn: "animate__fadeIn",
  };

  const bnOptions = { ...defaultOption, ...options };

  init = function () {
    var dropdowns = document
      .getElementById(bnOptions.selector)
      .getElementsByClassName("dropdown");

    Array.prototype.forEach.call(dropdowns, (item) => {
      //add animation
      if (bnOptions.animation) {
        const element = item.querySelector(".dropdown-menu");
        element.classList.add("animate__animated");
        element.classList.add(bnOptions.animateIn);
      }

      //hover effects
      item.addEventListener("mouseover", function () {
        this.classList.add("show");
        const element = this.querySelector(".dropdown-menu");
        element.classList.add("show");
      });

      item.addEventListener("mouseout", function () {
        this.classList.remove("show");
        const element = this.querySelector(".dropdown-menu");
        element.classList.remove("show");
      });
    });
  };

  init();
}
  
    
  $(document).ready(function(){

  new bootnavbar();
   $('#main_navbar').bootnavbar()

      })
 


      
    } 
}
</script>

